<template>
  <div class="header__theme">
    <div @click="changeTheme" class="header__imgs">
      <img v-if="themeDay"  class="header__img" src="../../../assets/day.svg" alt="day" />
      <img v-if="themeNight" class="header__img" src="../../../assets/night.svg" alt="night" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      themeNight: false,
      themeDay: true,
      mode: 'light',
    };
  },
  methods: {
    changeTheme() {
      this.themeNight = !this.themeNight;
      this.themeDay = !this.themeDay;
      if (this.mode.includes('light')) {
        this.mode = 'dark';
      } else {
        this.mode = 'light';
      }
      this.$emit('theme', {
        mode: this.mode,
      })
    }
  },
};
</script>

<style lang="sass" scoped>

.header__theme

  .header__imgs
    cursor: pointer

  .header__img
    height: 50px
    width: 50px



</style>
