<template>
  <div :class="themeMode.mode" class="weather__left">
    <div class="weather__content">
      <div class="weather__inner">
        <p :class="themeMode.mode" class="weather__temp">{{ arrayCity.temper }}&#xb0;</p>
        <img
          v-if="arrayCity.imgWeather !== undefined"
          :class="themeMode.mode"
          class="weather__img"
          :src="require(`../../../assets/${arrayCity.imgWeather}.png`)"
          alt="img weather"

        />
      </div>
      <p :class="themeMode.mode" class="weather__date">Today</p>
      <p :class="themeMode.mode" class="weather__time">Time: {{ nowTime }}</p>
      <p :class="themeMode.mode" class="weather__city">City: {{ arrayCity.cityName }}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: ["arrayCity", "themeMode"],
  data() {
    return {
      nowTime: new Date().toLocaleTimeString().slice(0, 5),
    };
  },
  created() {
    setInterval(() => {
      return this.nowTime = new Date().toLocaleTimeString().slice(0, 5);
    })
  }
  
};
</script>

<style lang="sass" scoped>
.weather__left.dark
  background: white
  box-shadow: 2px 5px 25px -3px rgba(180, 180, 180, 0.25)

.weather__left
    width: 30%
    height: 400px
    background: #4F4F4F
    border-radius: 20px
    padding: 20px
    color: white
    overflow: auto 
    transition: 0.3s ease all 

    p
      margin: 15px
      transition: 0.3s ease all

    p.dark 
      color: black

    .weather__temp
      font-size: 60px
      color: lightblue
      margin-bottom: 0

    .weather__date
      font-size: 25px
      margin-top: 0
      margin-bottom: 30px

    .weather__time
      font-size: 25px
      color: lightgray

    .weather__city
      color: lightgray

    .weather__inner 
      display: flex
      justify-content: space-between

</style>
